import axios from "axios";
import Url from "../../url/url";
import {swallErr, swallGood} from "../../alert/alert";
import errorHandler from "../error-handler/error-handler";

export const handleOk = (
    actionPermissionsForm,
    nameRole,
    dataPermissionsForm,
    setIsModalVisible,
    setValueName = () => {
    },
    dataRole = [],
    setDataRole = () => {
    },
    setSkeletonShow = () => {
    },
    text = 'создана',
    method = 'post',
    id = ''
) => {

    if (Object.keys({...actionPermissionsForm}).length && nameRole) {

        setSkeletonShow(true)

        axios({
            method,
            url: `${Url}/AdminPanel/Roles/${id}`,
            headers: {
                'Content-Type': 'application/json;charset=utf-8'
            },
            data: {
                name: nameRole,
                actionPermissions: Object.values(actionPermissionsForm),
                dataPermissions: {...dataPermissionsForm}
            }
        })
            .then(res => {
                let {data} = res
                let box = [...dataRole, data]

                swallGood('Готово', `Роль "${data.name}" ${text}`)
                if (id) {
                    setDataRole(data)
                } else {
                    setDataRole(box)
                }
                setSkeletonShow(false)
            })
            .catch(error => {
                errorHandler(handleOk, error, () => {
                    setSkeletonShow(false)
                })
            })

        setIsModalVisible(false)
    } else {
        swallErr('Ой...', 'Придумайте название и выберите хотя бы одно действие')
        setValueName(nameRole)
    }
};